import React, { Component } from "react";
import { Button, ButtonGroup } from 'reactstrap';
import "./ProjectMenu.css";
import { Document, Page } from 'react-pdf'
import ProjectName from "./ProjectName.js";
//import zip from "../zip.js/WebContent/zip.js";
import MyApp from "./MyApp";
import jsPDF from 'jspdf';
import 'jspdf-autotable'

// import JSZip from 'jszip';
// import JSZipUtils from 'jszip'
class ProjectMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projectName: "",
      RAL: "",
      value: 1
    }
    this.setProjectName = this.setProjectName.bind(this)
    this.handleChange = this.handleChange.bind(this);
  }




  handleChange(event, valve) {
    this.props.editValves(event.target.value, valve);
  }
  ralChange(event, valve) {
    this.props.editValveRAL(event.target.value, valve);
  }
  setProjectName(value) {
    this.setState({ projectName: value })
  }
  returnTooltip(valve) {
    return "Material: " + valve.material + "\n" +
      "Corrosivity: " + valve.corosivity + "\n" +
      "Temperature: " + valve.temperature + "\n" +
      "Operation: " + valve.actuator

  }

  showTitle() {
    return (
      <div className="svi">
        <div className="botun">
          Valve
            </div>
        <div className="brojach">
          #

            </div>
        <div className="brojach">

          RAL

        </div>

      </div>
    )
  }
  showValves() {
    if (this.props.valves.length > 0) {
      return (
        this.props.valves.map(valve => (
          <div className="svi">
            <div className="botun">
              <span title={this.returnTooltip(valve)}>
                <MyApp
                  buttontext={valve.code}
                  title={valve.code}
                  pdf={valve.pdf}></MyApp>
              </span>
            </div>

            <div className="brojach">
              <input className="valve-input" id={"counter" + valve.id.toString()} type="number" min="1" step="1" value={valve.counter} onChange={(e) => this.handleChange(e, valve)}
              ></input>
            </div>
            <div className="brojach">
              <input className="ralich-input" style={{ border: valve.border }}
                id={"ral" + valve.id.toString()} type="number" min="1000" max="9999" step="1"
                value={valve.ral} onChange={(e) => this.ralChange(e, valve)}
              ></input>


            </div>
            <div className="brojach">
              <Button color="danger" onClick={this.props.editValves.bind(this, "0", valve.id)}>Remove</Button>
            </div>
          </div>
        ))
      )
    }

    return (


      <h4>
        Please add valves to the project
      </h4>


    )
  }
  createPDF() {

    var doc = new jsPDF()
    // It can parse html:
    //doc.setFontSize(18)
    doc.setFontSize(11)
    doc.setTextColor(100)
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();

    today = dd + '/' + mm + '/' + yyyy;
    // jsPDF 1.4+ uses getWidth, <1.4 uses .width
    doc.text(today, 176, 10)
    doc.setFontSize(22)
    doc.setTextColor(0)

    var text = "Valve order for " + this.state.projectName + " project"

    let xOffset = (doc.internal.pageSize.width / 2);
    doc.text(text, xOffset, 30, 'center')

    // doc.setFontSize(12)
    // let textWidth = doc.getTextWidth("Project info:");
    // doc.line(5, 45, 5 + textWidth, 45)
    // doc.text("Project info:", 5, 45)

    // doc.setFontSize(16)
    // xOffset = (doc.internal.pageSize.width / 14);
    // text = "TEMPERATURE"
    // doc.text(text, xOffset * 3, 55, 'center')
    // text = "CORROSION CLASS"
    // doc.text(text, xOffset * 7, 55, 'center')
    // text = "RAL"
    // doc.text(text, xOffset * 11, 55, 'center')
    // text = this.props.valves[0].temperature
    // doc.text(text, xOffset * 3, 65, 'center')
    // text = this.props.valves[0].corosivity
    // doc.text(text, xOffset * 7, 65, 'center')
    // text = "1101"
    // doc.text(text, xOffset * 11, 65, 'center')

    var mybody = []
    this.props.valves.map(valve => (
      mybody.push([valve.code, valve.counter, valve.corosivity, valve.temperature,
      valve.material, valve.size, valve.pressure, valve.joining, valve.type, valve.actuator, valve.ral])
    ))
    // Or use javascript directly:
    doc.setFontSize(10)
    doc.autoTable({
      head: [['Code', '#', 'C.C.', 'Temp.', 'Material', 'Size', 'Pressure', "Connection", "Type", "Operation", "RAL"]],
      body: mybody,
      startY: 75


    })


    doc.setFontSize(10)
    doc.setTextColor(10)

    doc.text("This order has been generated using OMV Indoil Valve selection app and must be confirmed by the customer.", 14, doc.autoTable.previous.finalY + 10)
    doc.save(this.state.projectName + ".pdf")

    // JSZipUtils.getBinaryContent("pdfs/1.pdf", function (err, data) {
    //   if (err) {
    //     throw err; // or handle the error
    //   }
    // })
    // var zip = new JSZip();
    // zip.file("Some.pdf", data, { binary: true });
    // content = zip.generate();


  }
  sendMail() {
    var customer, body, quantities, product_codes;
    body = "Dear OMV-INDOIL,\nI would like to order:\n";
    this.props.valves.map(valve => (
      body += valve.counter + " of: " + valve.code + " " + "" + valve.corosivity + " " + valve.temperature + " " +
      valve.material + " " + valve.size + " " + valve.pressure + " " + valve.joining +
      " " + valve.type + " " + valve.actuator + " " + valve.ral + "\n"
    ))


    body += "\nYours truly,\nhackerman"
    body += '\n\n\nThis message is generated automatically using OMV Valve Selection App'

    var subject = this.state.projectName + " Order";

    window.open('mailto:dummy@dummy.com?body=' + encodeURIComponent(body) + '&subject=' + encodeURIComponent(subject));

    //zip.workerScriptsPath = '../zip.js/WebContent/';

  }



  render() {
    return (
      <div className="full">
        <ProjectName
          nameHandler={this.setProjectName} />
        <div>
          <span>{this.showTitle()}</span>
        </div>
        <div className="scrollich">
          <span>{this.showValves()}</span>
        </div>
        <h3></h3>
        <div className="custombottom">
          <div className="custombottom-left">
            <Button disabled={!(this.state.projectName != "") || this.props.valves.length <= 0} color="primary" onClick={this.createPDF.bind(this)}>Generate PDF</Button>
          </div>
          <div className="custombottom-right">
            <Button disabled={!(this.state.projectName != "") || this.props.valves.length <= 0} color="primary" onClick={this.sendMail.bind(this)}>Send Mail</Button>
          </div>
        </div>
      </div>
    );
  }
}

export default ProjectMenu;