import React, { useState } from 'react';
import { Button, ButtonGroup, Modal } from 'reactstrap';

const ConfirmButton = (props) => {


    const myClick = () => (
        props.handler(),
        props.subreset(true)
        //console.log(rSelected, x, rSelected===1)
    )

    //console.log(Object.keys(views))
    //console.log(views)
    if (props.doshow) {
        return (
            <div>
                <h5></h5>
                <ButtonGroup>
                    <Button size="lg" color="success" onClick={() => myClick()} disabled={props.visible || props.disabled} >Submit</Button>
                </ButtonGroup>
            </div>
        );
    }
    return (
        <h5></h5>
    );
}

export default ConfirmButton;