
import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import "./Login.css";
import { Col, Form, FormGroup, Label, Input, FormText } from 'reactstrap';

const MyApp = (props) => {
    const {
        buttonLabel,
        className
    } = props;
    var username;
    var password;
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    return (
        <div>
            <Button color="info" onClick={toggle}>{props.buttontext}</Button>
            <Modal isOpen={modal} toggle={toggle} className={className} contentClassName="custom-login-style">
                <ModalHeader toggle={toggle}>{props.title}</ModalHeader>
                <ModalBody >
                    <Form>
                    <FormGroup row>
                        <Label for="exampleEmail" sm={2}>Email</Label>
                        <Col sm={10}>
                            <Input type="text" name="text" id="text" placeholder="username"
                             onKeyDown={e => {
                                username = e.target.value;
                            }}
                            onKeyUp={e => {
                                username = e.target.value;
                            }}
                           />
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Label for="examplePassword" sm={2}>Password</Label>
                        <Col sm={10}>
                            <Input type="password" name="password" id="examplePassword" placeholder="*******" 
                                  onKeyDown={e => {
                                    password = e.target.value;
                                }}
                                onKeyUp={e => {
                                    password = e.target.value;
                                }}/>
                        </Col>
                    </FormGroup>
                    </Form>
                </ModalBody>
                <ModalFooter>
                    {/* <Button color="primary" onClick={toggle}>Do Something</Button>{' '} */}
                    <Button color="secondary" onClick={() => props.passcheck(username, password)}>Submit</Button>

                    <Button color="secondary" onClick={toggle}>Cancel</Button>
                </ModalFooter>
            </Modal>
        </div>
    );
}

export default MyApp;