import React, { useState } from 'react';
import { Button, ButtonGroup } from 'reactstrap';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import "./PrimaryToggle.css";
import Image from 'react-bootstrap/Image'


function returnTooltip() {

    return <div><img src="https://i.stack.imgur.com/t6DjL.png"></img></div>
}
const PrimaryToggle = (props) => {
    const [rSelected, setRSelected] = useState(null);
    const [dropdownOpen, setOpen] = useState(false);
    var dropdownText = "Ventili za specijalne namjene"
    const toggle = () => setOpen(!dropdownOpen);

    const myClick = (x, e) => (
        props.handler(props.options[x]),
        setRSelected(x)
        //props.resetValves()
        //console.log(rSelected, x, rSelected===1)
    )

    const dropdownClick = (text, x) => (
        props.handler(text),
        setRSelected(x),
        dropdownText = text
    )

    const imgarray = ["oil_sampling", "vacuuming", "vacuuming", "piping", "vacuuming", "dga"]
    //props.resetValves()
    //console.log(rSelected, x, rSelected===1)

    // const returnTooltip =() =>{
    //             return       

    // }
    var arrayLength = props.options.length;
    const views = [];
    if (false && props.name == "Application" && arrayLength > 1) { //disabled atm
        for (var step = 0; step < arrayLength - 1; step++) {
            let y = step;

            views.push(<Button key={y} disabled={props.disabled} size="lg" color="primary" onClick={() => myClick(y, y + 1)} active={rSelected == y}>{props.options[step]}</Button>
            )
        }
        console.log(dropdownText)
        let y = arrayLength;
        views.push(<ButtonDropdown isOpen={dropdownOpen} toggle={toggle} active={rSelected == y} >
            <DropdownToggle id="dropdown" caret color="primary" size="lg">
                {dropdownText}
            </DropdownToggle>
            <DropdownMenu>
                <Button color="primary"
                    onClick={() => dropdownClick("Ventili za izjednaavanje tlakova unutar konzervatora prilikom vakumiranja i punjenja", y)}>
                    Ventili za izjednaavanje tlakova unutar konzervatora prilikom vakumiranja i punjenja</Button>
                <Button color="primary"
                    onClick={() => dropdownClick("Ventili za brzodjelujui odunik", y)}>
                    Ventili za brzodjelujui odunik</Button>
                <Button color="primary"
                    onClick={() => dropdownClick("Ventili za DGA ureaj", y)}>
                    Ventili za DGA ureaj</Button>
            </DropdownMenu>
        </ButtonDropdown>)

    }
    else if (props.name == "Application" && arrayLength > 1) {
        for (var step = 0; step < arrayLength; step++) {
            let y = step;
            views.push(

                <div className="tooltipz">
                    <Button key={y} disabled={props.disabled} size="lg" color="primary" onClick={() => myClick(y, y + 1)} active={rSelected == y}>{props.options[step]}</Button>
                    <span>
                        <img className="callout" src={"images/"+imgarray[step] + ".png"} />
                    </span>
                </div>
            )
            // views.push(
            // <span title={returnTooltip()}> 
            // <Button key={y} disabled={props.disabled} size="lg" color="primary" onClick={() => myClick(y, y + 1)} active={rSelected == y}>{props.options[step]}</Button>
            // </span>
            // )
        }
    }
    else {
        for (var step = 0; step < arrayLength; step++) {
            let y = step;

            views.push(<Button key={y} disabled={props.disabled} size="lg" color="primary" onClick={() => myClick(y, y + 1)} active={rSelected == y}>{props.options[step]}</Button>
            )

        }
    }
    //console.log(Object.keys(views))
    //console.log(props.disabled)
    if (props.doshow) {
        return (
            <div>
                <h5>{props.name}</h5>
                <ButtonGroup>
                    {views}
                </ButtonGroup>
            </div>
        );
    }
    return (
        <h5></h5>
    );

}

export default PrimaryToggle;