import React, { Component, Fragment } from "react";
import Header from "./components_js/Header";
import Home from "./components_js/Home";
import ProjectMenu from "./components_js/ProjectMenu";
import ValveSelection from "./components_js/ValveSelection";
import "./App.css";
import jsPDF from 'jspdf';

import { Document, Page } from 'react-pdf';
import MyApp from './components_js/MyApp'
import 'jspdf-autotable'
import { rals } from "./Data/rals.js"
import { allValves, corosivityDict, sortedEu, sortedAnsi } from "./Data/Data2.js"

import { makeStyles } from '@material-ui/core/styles';
import Login from './components_js/Login'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
//import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';



class App extends Component {
  constructor(props) {
    super(props);
    this.projectName = "xyz"
    this.state = {
      loggedInText: "Please log in",
      loggedIn: false,
      addedValves: []
    };
    this.setLoggedInText = this.setLoggedInText.bind(this)
    this.addValve = this.addValve.bind(this)
    this.editValves = this.editValves.bind(this)
    this.passcheck = this.passcheck.bind(this)
    this.editValveRAL = this.editValveRAL.bind(this)
  }



  setLoggedInText(value) {
    this.setState({ loggedInText: value })
  }

  editValves(myvalue, myvalve) {
    let valvez = this.jsonCopy(myvalve)

    let copyvalves = this.state.addedValves;
    ////console.log(copyvalves)
    let value = parseInt(myvalue)
    if (copyvalves.length > 0) {
      let check = copyvalves.findIndex(valve => (valve.id === valvez.id
        && valve.corosivity === valvez.corosivity &&
        valve.temperature === valvez.temperature &&
        valve.actuator === valvez.actuator))
      if (value <= 0 || value == NaN) {
        if (window.confirm("You are about to remove " + copyvalves[check].code + ". Are you sure?")) {
          copyvalves.splice(check, 1)
        }
      }
      else {
        copyvalves[check].counter = value
      }

      this.setState({
        addedValves: copyvalves
      })
    }
  }

  editValveRAL(value, myvalve) {
    let valvez = this.jsonCopy(myvalve)

    let checkvalue = parseInt(value)
    let copyvalves = this.state.addedValves;

    let check = copyvalves.findIndex(valve => (valve.id === valvez.id
      && valve.corosivity === valvez.corosivity &&
      valve.temperature === valvez.temperature &&
      valve.actuator === valvez.actuator))
    let myral = "RAL" + value
    if (myral in rals) {
      copyvalves[check].border = "3px solid " + rals[myral]
    }
    else {

      copyvalves[check].border = "1px #000000 dotted"

    }
    copyvalves[check].ral = value
    this.setState({
      addedValves: copyvalves
    })


  }

  jsonCopy(src) {
    return JSON.parse(JSON.stringify(src));
  }

  addValve(myvalue, temperature, corosivity, actuator) {
    let value = this.jsonCopy(myvalue)
    ////console.log(value)

    value.temperature = temperature
    value.corosivity = corosivity
    value.actuator = actuator
    ////console.log(value)

    let copyvalves = this.state.addedValves.slice();
    //////console.log(copyvalves, this.state.addedValves)
    let check = copyvalves.findIndex(valve => (valve.id === value.id
      && valve.corosivity === value.corosivity &&
      valve.temperature === value.temperature &&
      valve.actuator === value.actuator))
    //////console.log(copyvalves, this.state.addedValves)

    //console.log(check, value, copyvalves[check])
    if (check >= 0) {
      // //console.log(copyvalves[check].id === value.id
      //   , copyvalves[check].corosivity === value.corosivity,
      //   copyvalves[check].temperature === value.temperature,
      //   copyvalves[check].actuator === value.actuator)
    }
    if (check >= 0) {
      //let idx = copyvalves.findIndex(valve => valve.id === value.id)
      alert("You have already added this valve. We have increased its counter by one.")
      copyvalves[check].counter = parseInt(copyvalves[check].counter) + 1

      this.setState({
        addedValves: copyvalves
      })
    }
    else {
      value.counter = 1;
      value.ral = "";
      value.border = "1px #000000 dotted"

      this.setState({
        addedValves: this.state.addedValves.concat([value])
      })
    }

  }

  passcheck(myUsername, myPass) {
    //console.log(myUsername, myPass)
    if (myPass === "test1234" && myUsername === "test") {
      this.setState({
        loggedIn: true
      })
      return true
    }
    return false
  }
  render() {

    //console.log(this.state)

    if (!this.state.loggedIn) {
      return (
        <div style={{ backgroundColor: "#D6DBDF" }} >
          <div style={{ backgroundColor: "#D6EAF8" }} className="header">
            <Header></Header>
            <AppBar position="static">
              <Toolbar>
                <Login
                  buttontext={"Please Log In"}
                  usernames={["test"]}
                  passcheck={this.passcheck}
                ></Login>
              </Toolbar>
            </AppBar>
          </div>
        </div>);
    }
    return (


      <div style={{ backgroundColor: "#D6DBDF" }} >
        <div style={{ backgroundColor: "#D6EAF8" }} className="header">
          <Header></Header>
          <AppBar position="static">
            <Toolbar>

              <Button onClick={() => this.setLoggedInText("Logged in")} color="inherit">{"Logged In"}</Button>
            </Toolbar>
          </AppBar>
        </div>
        <div className="row">
          <div style={{ backgroundColor: '#FAFAFB' }} className="left">
            <ProjectMenu
              projectName={this.projectName}
              valves={this.state.addedValves}
              editValves={this.editValves}
              editValveRAL={this.editValveRAL} />
          </div>
          <div className="right">
            <ValveSelection
              myAllValves={allValves}
              addHandler={this.addValve} />
          </div>

        </div>
      </div>



    );
  }


}

export default App;